import React from "react"
import Link from "gatsby-link"
import YellowButtonMD from "../components/Buttons/YellowButtonMD"
import Hobbits from "../pages/images/race_hobbit.jpg";
import Humans from "../pages/images/race_human.jpg";
import Elves from "../pages/images/race_woodelf.jpg";
import Dwarves from "../pages/images/race_dwarf.jpg";
import Beornings from "../pages/images/race_beorning.jpg";
import UrukHais from "../pages/images/race_urukhai.jpg";
import Orcs from "../pages/images/race_orc.jpg";
import OlogHais from "../pages/images/race_ologhai.jpg";
import UrukLhuths from "../pages/images/race_uruklhuth.jpg";
import Haradrims from "../pages/images/race_haradrim.jpg";

class RacePageTemplate extends React.Component {
    handleRaceImage(title) {
        switch(title)
        {
            case "Hobbits":
                return Hobbits;
            case "Humans":
                return Humans;
            case "Wood Elves":
                return Elves;
            case "Dwarves":
                return Dwarves;
            case "Beornings":
                return Beornings;
            case "Uruk-Hais":
                return UrukHais;
            case "Common Orcs":
                return Orcs;
            case "Olog-Hais":
                return OlogHais;
            case "Uruk-Lhuths":
                return UrukLhuths;
            case "Haradrims":
                return Haradrims;
            default:
                break;
        }
    }
    render() {
        const races = this.props.data.markdownRemark;
    
        return (
            <div className="container">
                <div className="col-lg-4">
                    <img src={this.handleRaceImage(races.frontmatter.title)} className="race-profile"/>
                </div>
                <div className="col-lg-8">
                    <h1>{races.frontmatter.title}</h1>
                    <div dangerouslySetInnerHTML={{__html: races.html}} />
                </div>
            </div>
        );
    }
}

export default RacePageTemplate;

export const query = graphql`
    query RacePageQuery($path: String!) {
        markdownRemark(frontmatter: {path: {eq: $path}}) {
            html
            frontmatter {
                title
                raceid
            }
        }
    }
`


